const CitizenshipTabComponent = () => {
  const [isSurveyActive, setIsSurveyActive] = useState(false);
  const [isSurveyComplete, setIsSurveyComplete] = useState(false);

  const [questions] = useState([
    {
      id: "q1",
      question: "What is your country of origin?",
      description: "We need to know your country of origin to determine your eligibility for citizenship.",
      options: countries.map(country => ({
        value: country.name,
        points: 1
      })),
      type: "dropdown"
    },
    {
      id: "q2",
      question: "What is your annual income?",
      description: "We need to know your annual income to determine if you are fit for a job in Canada",
      options: [
        {
          value: "Less than $10,000",
          points: 3
        },
        {
          value: "Between $10,000 and $30,000",
          points: 10
        },
        {
          value: "Between $30,000 and $50,000",
          points: 15
        },
        {
          value: "Between $50,000 and $100,000",
          points: 20
        },
        {
          value: "More than $100,000",
          points: 25
        }
      ],
      type: "dropdown"
    },
    {
      id: "q3",
      question: "What is your age?",
      description: "We need to know your age to determine if you are fit for a job in Canada",
      options: [
        {
          value: "Less than 18",
          points: 3
        },
        {
          value: "Between 18 and 25",
          points: 10
        },
        {
          value: "Between 25 and 35",
          points: 15
        },
        {
          value: "Between 35 and 45",
          points: 20
        },
        {
          value: "Older than 45",
          points: 25
        }
      ],
      type: "dropdown"
    },
    {
      id: "q4",
      question: "What is your employment status?",
      description: "We need to know your employment status to determine if you are fit for a job in Canada",
      options: [
        {
          value: "Full-time",
          points: 12
        },
        {
          value: "Part-time",
          points: 10
        },
        {
          value: "Self-employed",
          points: 7
        },
        {
          value: "Unemployed",
          points: 10
        }
      ],
      type: "dropdown"
    },
    {
      id: "q5",
      question: "What is your highest level of education?",
      description: "We need to know your highest level of education to determine if you are fit for a job in Canada",
      options: [
        {
          value: "High school",
          points: 10
        },
        {
          value: "Bachelor's degree",
          points: 15
        },
        {
          value: "Master's degree",
          points: 20
        },
        {
          value: "Doctorate degree",
          points: 25
        },
        {
          value: "Other",
          points: 10
        }
      ],
      type: "dropdown"
    },
    {
      id: "q6",
      question: "What is your marital status?",
      description: "We need to know your marital status to determine if you are fit for a job in Canada",
      options: [
        {
          value: "Single",
          points: 7
        },
        {
          value: "Married",
          points: 9
        },
        {
          value: "Widowed",
          points: 11
        },
        {
          value: "Divorced",
          points: 6
        },
        {
          value: "Other",
          points: 6
        }
      ],
      type: "dropdown"
    },
    {
      id: "q7",
      question: "Is someone in your household currently in Canada?",
      description: "We need to know if someone in your household is currently in Canada to determine if you are fit for citizenship",
      options: [
        {
          value: "Yes",
          points: 10
        },
        {
          value: "No",
          points: 5
        }
      ],
      type: "dropdown"
    },
    {
      id: "q8",
      question: "Do you have a criminal record?",
      description: "We need to know if you have a criminal record to determine if you are fit for citizenship",
      options: [
        {
          value: "Yes",
          points: 3
        },
        {
          value: "No",
          points: 20
        }
      ],
      type: "dropdown"
    },
    {
      id: "q9",
      question: "Are you coming to Canada for diplomatic or business reasons?",
      description: "We need to know if you are coming to Canada for diplomatic or business reasons to determine if you are allowed to enter Canada",
      options: [
        {
          value: "Yes",
          points: 10
        },
        {
          value: "No",
          points: 5
        }
      ],
      type: "dropdown"
    },
    {
      id: "q10",
      question: "Have you ever been arrested and incarcerated in another country?",
      description: "We need to know if you have ever been arrested and incarcerated in another country to determine if you are allowed to enter Canada",
      options: [
        {
          value: "Yes",
          points: -6
        },
        {
          value: "No",
          points: 20
        }
      ],
      type: "dropdown"
    },
    {
      id: "q11",
      question: "Are you coming to Canada for medical reasons?",
      description: "We need to know if you are coming to Canada for medical reasons to determine if you are allowed to enter Canada. Medical reasons include: medical treatment, medical emergencies, or medical repatriation.",
      options: [
        {
          value: "Yes",
          points: 10
        },
        {
          value: "No",
          points: 0
        }
      ],
      type: "dropdown"
    },
    {
      id: "q12",
      question: "Are you coming to Canada for humanitarian reasons?",
      description: "We need to know if you are coming to Canada for humanitarian reasons to determine if you are allowed to enter Canada. Humanitarian reasons include: humanitarian aid, humanitarian relief, or humanitarian repatriation.",
      options: [
        {
          value: "Yes",
          points: 10
        },
        {
          value: "No",
          points: 0
        }
      ],
      type: "dropdown"
    },
    {
      id: "q13",
      question: "Have you ever been extradited from another country?",
      description: "We need to know if you have ever been extradited from another country to determine if you are allowed to enter Canada",
      options: [
        {
          value: "Yes",
          points: -10
        },
        {
          value: "No",
          points: 2
        }
      ],
      type: "dropdown"
    }
  ]);

  const [answers, setAnswers] = useState({});

  const handleSurveyClick = () => {
    setIsSurveyActive(true);
    console.log(questions);
  };

  const isEligible = () => {
    const points = Object.values(answers).reduce((acc, answer) => {
      const question = questions.find(question => question.options.find(option => option.value === answer));
      return acc + question.options.find(option => option.value === answer).points;
    }, 0);

    return points >= 53;
  }

  return (
    <>
      {isSurveyActive ? (
        <>
          {isSurveyComplete ? (
            <>
              <p>Based on your results...</p>
              {isEligible() ? (
                <Alert
                  intent="success"
                  title="You will likely be allowed entry to Canada."
                >Although this survey determined you're eligible, it is all up to the border inspector that processes your documentation.</Alert>
              ) : (
                <Alert
                  intent="danger"
                  title="You will likely be denied entry to Canada."
                >Although this survey determined you're not eligible, it is all up to the border inspector that processes your documentation.</Alert>
              )}
            </>
          ) : (
            <>
              <h3>Survey</h3>
              <div
                style={{
                  borderBottom: "1px solid #edeff5",
                  marginBottom: "20px",
                }}
              />
              {questions.map((q) => (
                <div
                  key={q.id}
                  style={{
                    columnGap: "20px",
                    marginBottom: "30px",
                  }}
                >
                  <h5>{q.question}</h5>
                  <p>{q.description}</p>
                  {q.type === "dropdown" ? (
                    <Combobox
                      autocompleteProps={{
                        title: "Choose an option",
                      }}
                      items={q.options.map((option) => option.value)}
                      placeholder="Select an option"
                      onChange={(selected) => {
                        setAnswers({
                          ...answers,
                          [q.id]: selected,
                        });
                      }}
                    />
                  ) : (
                    <></>
                  )}
                </div>
              ))}

              {(questions.length === Object.keys(answers).length ? (<></>) : (
                <Alert
                  intent="warning"
                  title="You have not completed all of the questions."
                  marginBottom={20}
                />
              ))}

              <Button
                onClick={() => {
                  setIsSurveyComplete(true);
                }}
                appearance="primary"
                {
                  ...(questions.length === Object.keys(answers).length ? {} : { disabled: true })
                } 
              >
                Finish
              </Button>
            </>
          )}
        </>
      ) : (
        <>
          <p>Here you can see if you are eligible to be a Canadian citizen.</p>
          <p>To begin the survey, click the button below.</p>

          <Button
            appearance="primary"
            marginTop="20px"
            onClick={() => {
              handleSurveyClick();
              console.log(answers);
            }}
          >
            Start Eligibility Survey
          </Button>
        </>
      )}
    </>
  );
}

export default CitizenshipTabComponent;